--------------------------------------------------------------*/
.error-container {
  line-height: 1.4;
  text-align: center;
}

.error-container .header h1 {
  font-size: 220px;
  letter-spacing: 10px;
  margin: 0px;
  text-transform: uppercase;
}

.error-container h2 {
  font-size: 50px;
  margin-top: 10px;
  margin-bottom: 15px;
  text-transform: uppercase;
}

